                top: 30px;
                left: 30px;
                width: 40px;
                height: 40px;
                background-size: 25px 25px;
                background-repeat: no-repeat;
                background-image: url("../img/혜리\ 보라로고.png");
                object-fit: cover;
            }
            &.yellow {
                position: fixed;
                top: 30px;
                left: 30px;
                width: 40px;
                height: 40px;
                background-size: 25px 25px;
                background-repeat: no-repeat;
                object-fit: cover;
                background-image: url("../img/혜리\ 노랑로고.png");
            }
        }
        .go.login.btn {
            @include setBarBtn(transparent,$purple);
            right: 150px;
        }
        .go.fee-page.btn {
            @include setBarBtn($purple, transparent);
            color: $yellow;
            right: 30px;
            width: 105px;
            // background-repeat: no-repeat;
        }
    }
    
    .signup-button-box {
        display: flex;
        justify-content: center;
        .go-signup-button {
            @include setGoSignUpButton(black, white);
            &.book, 
            &.contents {
                @include setGoSignUpButton($purple, white);
            }
            &.subscribe {
                @include setGoSignUpButton($white, $dark-purple);
            }
        }
    }
    
    #intro-container {
        @include setContainer($yellow);
        min-height: 1300px;
        .logo {
            width: 130px;
            padding-top: 110px; 
            background-repeat: no-repeat;
            img {
                width: 100%;
            }
        }
    
        .copy-wrap {
            position: relative;
            top: -500px;
            z-index: 1;
            opacity: 0;
            animation: moveCopies 1.5s ease-out 1.5s forwards;
            .intro-main-copy {
                @include setMainCopy($black);
            }
            
            .intro-sub-copy {
                @include setSubCopy($black);
                strong {
                    animation: fontColorChange 2s ease-out 2s forwards;
                }
            }
        }
    
        .img-wrap {
            position: relative;
            top: -30px;
            right: -30%;
            width: 70%;
            max-width: 967px;
    
            .book1-img {
                @include setIntroBookMovement(5%, 15%, 36%);
                animation: moveIntroBooks 2s ease-out forwards;
            }
            .book2-img {
                @include setIntroBookMovement(4%, 15%, 45%);
                animation: moveIntroBooks 2s ease-out 1s forwards;
            }
        }
    }
    
    #slogan-container {
        @include setContainer($yellow);
        margin-top: 60px;
        padding-left: 0px;
        min-height: 1300px;
        display: flex;
        position: relative;
        flex-direction: column;
        align-items: center;
        // justify-content: center;
        .copy-and-logo-container {
    
            display: flex;
            z-index: 2;
            margin-bottom: 200px;
            flex-direction: column;
            justify-content: space-between;
            align-items: center;
            .slogan-main-copy {
                position: relative;
                z-index: 11;
                padding-top: 130px;
                font-family: $slogan-font;
                font-size: 60px;
                font-weight: 300;
                letter-spacing: -5px;
                strong {
                    font-family: $slogan-font;
                    font-weight: 900;
                    color: $purple;
                }
            }
        }
        .shelf-image1 {
            position: absolute;
            top: 50px;
            right: 0;
        }
        .shelf-image2 {
            position: absolute;
            top: 200px;
            left: 0;
        }
        .shelf-image3 {
            position: absolute;
            top: 350px;
            right: 150px;
        }
    
        .slogan-and-logo {
            position: absolute;
            width:150px;
            bottom: 15%;
        }
    }
    
    #book-introduction-container {
        @include setContainer($yellow);
        padding-bottom: 100px;
        margin-top: 60px;
        min-height: 1024px;     
        .book.main-copy {
            @include setMainCopy($black);
            // font-size: $main-copy-size;
        }
        .book.sub-copy  {
            @include setSubCopy($purple);
        }
        .descriptions {
            margin-top: 30px;
        }
        .description {
            @include setDescription();
            position: relative;
            z-index: 99;
        }
        .books-img-wrap {
