                    z-index: 2;
                    top: -40%;
                    width: 100%;
                    display: flex;
                    justify-content: center;
                }
                .content-second-img {
                    position: relative;
                    top: -35%;
                    left: 1%;
                    width: 60%;
                }
            }
        }
    }
    
    
    #subscribe-option-introduction-container {
        @include setContainer($dark-purple);
        overflow-x: visible;
        min-height: 2000px;
