        }
        .sub-copy {
            @include setSubCopy($yellow);
        }
        .descriptions {
            margin-top: 30px;
        }
        .description {
            @include setDescription()
        }
        .presents-wrapper {
            position: absolute;
            right: 10%;
        }
        .present-box {
            max-width: 400px;
            width: 100%;
            &.active {
                animation: presentBoxActive 1s forwards;
            }
            .head {
                width: 255px;
                height: 128px;
                z-index: 101;
                &.animation {
                    animation: presentBoxHeadRotateAnimation 1s 0.5s ease-out forwards;
                }
            }
            .body {
                width: 225px;
                height: 222px;
                &.animation {
                    animation: presentBoxBodyRotateAnimation 1s 0.4s ease-out;
                }
            }
        }
        .present-infos {
            display: flex;
            flex-direction: column;
            .present-info {
                max-width:400px;
                width:100%;
            }
        }
        .original-logo {
            display: inline;
            position: relative;
            top: 300px;
            left: 55px;
        }
    }
    
    #fee-introduction-container {
        * {
            color: $white;
            list-style: none;
        }
        padding-top: 60px;
        padding-left: 50px;
        min-height: 1024px;
        background-color: $black;
    
        .fee.sub-copy {
            @include setSubCopy($white);
        }
        .fee.main-copy {
            @include setMainCopy($white);
            font-size: 50px;
        }
        .fee-description-wraps {
            position: relative;
            bottom: 50px;
            display: flex;
            flex-direction: row;
            width: calc(50% - 4px);
            position: relative;
            left: 40%;
            div + div {
                margin-left: 4px;
            }
        }
        .ebook-subscription-fee-wrap {
            @include setSubscriptionFeeWrap(60%, 20px);
            .fee-wrap-header {
                @include setFeeWrapHeader($yellow);
            }
            .fee-wrap-head-copy {
                @include setFeeWrapHeadCopy(40px.2 )
            }
            .fee {
                @include setSubscriptionWrapperHeadCopy(#A69B4D, 24px, 32px);
            }
            .fee-wrap-body {
                @include setFeeWrapBody();
                padding-bottom: 150px;
            }
            .fee-wrap-btn-box {
                @include setFeeWrapBtnBox();
                .month-subscribe-btn {
                    @include setSubscriptionWrapperBtn($yellow, black);
                }
                .year-subscribe-btn {
                    @include setSubscriptionWrapperBtn(#FFC004, black);
                }
            }
        }
        .paper-subscription-fee-wrap {
            @include setSubscriptionFeeWrap(60%, 20px);
            .fee-wrap-header {
                @include setFeeWrapHeader(#6A31A4);
                * {
                    color: white;
                }
            }
            .fee-wrap-head-copy {
                @include setFeeWrapHeadCopy(40px);
            }
            .fee {
                @include setSubscriptionWrapperHeadCopy(#c5c5c5, 24px, 32px);
            }
            .fee-wrap-body {
                @include setFeeWrapBody();
                padding-bottom: 100px;
            }
            .fee-wrap-btn-box {
                @include setFeeWrapBtnBox();
                display: flex;
                .month-subscribe-btn {
                    @include setSubscriptionWrapperBtn(#A87DDF,white );
                }
                .year-subscribe-btn {
                    @include setSubscriptionWrapperBtn(#6A31A4, white);
                }
            }
        }
    }
    
}


