            bottom: 15%;
        }
    }
    
    #book-introduction-container {
        @include setContainer($yellow);
        padding-bottom: 100px;
        margin-top: 60px;
        min-height: 1024px;     
        .book.main-copy {
            @include setMainCopy($black);
            // font-size: $main-copy-size;
        }
        .book.sub-copy  {
            @include setSubCopy($purple);
        }
        .descriptions {
            margin-top: 30px;
        }
        .description {
            @include setDescription();
            position: relative;
            z-index: 99;
        }
        .books-img-wrap {
            display: flex;
            position: relative;
            width: 100%;
            right: -10%;
            align-items: flex-end;
            flex-wrap: wrap;
            margin-top: -10%;
            z-index: 1;
            opacity: 0.5;
            .books-wrap-book {
                margin: 0.5%;
                min-width: 6%;
                width: 15%;
                height: auto;
            }
            .show {
                display: relative;
                margin: 1%;
                min-width: 6%;
                width: 8%;
                height: auto;
                transform: translate(80px, 0);
            }
        }
    }
    
    #genre-introduction-container {
        @include setContainer($white);
        min-height: 1024px;
        transition: all 1s;
        &::before {
            overflow-x: hidden;
            position: absolute;
            display: block;
            left: -50px;
            content:'';
            width: 103%;
            background-color: $white;
        }
        &.black {
            &::before {
                overflow-x:hidden;
                position: absolute;
                display: block;
                left: -50px;
                content:'';
                width: 103%;
                animation: genrePageGradientEffect 0.7s ease-in-out;
            }
            background-color: $black;
            * {
                color: white;
            }
        }
        .genre.main-copy {
            @include setMainCopy($black);
        }
        .genre.sub-copy {
            @include setSubCopy($purple);
        }
        .descriptions {
            margin-top: 30px;
        }
        .description {
            @include setDescription();
        }
        .rotated-box-wrapper {
            position: relative;
            top: -170px;
            left: -5%;
        }
        .rotated-box {
            &:nth-child(1) {
                @include setRotateBox(-20px, 100px, 35px, 35px, $yellow, 10deg);
            }
            &:nth-child(2) {
                @include setRotateBox(0, 50px, 50px, 50px, #EDD943, 30deg);
                z-index: 2;
            }
            &:nth-child(3) {
                @include setRotateBox(-20px, 0px, 60px, 60px, $purple, 20deg);
            }
            &:nth-child(4) {
                @include setRotateBox(-40px, -100px, 60px, 60px, $white, 10deg);
            }
            &:nth-child(5) {
                @include setRotateBox(-20px, -120px, 20px, 20px, #57CAF0, 60deg);
                z-index: 1;
            }
            &:nth-child(6) {
                @include setRotateBox(-25px, -140px, 50px, 50px, #EDD943, 30deg);
            }
            &:nth-child(7) {
                @include setRotateBox(-10px, -300px, 35px, 35px, $dark-purple, 40deg);
                z-index: 1;
            }
        }
    }
    
    #contents-introduction-container {
        position: relative;
        overflow-y: hidden;
        @include setContainer($white);
        padding-left: 0;
        min-height: 1024px;
        // position: relative;
        // display: block;
        .contents.main-copy {
            @include setMainCopy($black);
            padding-left: 5%;
        }
        .contents.sub-copy {
            @include setSubCopy($purple);
            padding-left: 5%;
        }
        .descriptions {
            margin-top: 30px;
            padding-left: 5%;
        }
        .description {
            @include setDescription();
        }
        .contents-img-box {
            position: relative;
            // z-index: 1;
            // top: 17%;
            // right: 0;
            margin-top: 50%;
            width: 100%;
            display: flex;
            justify-content: center;
            flex-wrap: wrap;
            .content {
                min-width: 85%;
                position: relative;
                // &.first {
                //     top: -100px;
