    #subscribe-option-introduction-container {
        @include setContainer($dark-purple);
        padding-left: 0;
        overflow-x: visible;
        min-height: 2000px;
        * {
            color: white;
        }
        .main-copy {
            @include setMainCopy($white);
            padding-left: 5%;
        }
        .sub-copy {
            @include setSubCopy($yellow);
            padding-left: 5%;
        }
        .descriptions {
            margin-top: 30px;
            padding-left: 5%;
        }
        .description {
            @include setDescription()
        }
        .presents-wrapper {
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            position: absolute;
        }
        .present-box {
            max-width: 400px;
            width: 100%;
            &.active {
                animation: presentBoxActive 1s forwards;
            }
            .head {
                width: 255px;
                height: 128px;
                z-index: 101;
                &.animation {
                    animation: presentBoxHeadRotateAnimation 1s 0.5s ease-out forwards;
                }
            }
            .body {
                width: 225px;
                height: 222px;
                &.animation {
                    animation: presentBoxBodyRotateAnimation 1s 0.4s ease-out;
                }
            }
