    #fee-introduction-container {
        * {
            color: $white;
            list-style: none;
        }
        position: relative;
        padding-top: 60px;
        padding-left: 0px;
        min-height: 1300px;
        background-color: $black;
    
        .fee.sub-copy {
            @include setSubCopy($white);
            padding-left: 5%;
        }
        .fee.main-copy {
            @include setMainCopy($white);
            padding-left: 5%;
            font-size: 50px;
        }
        .fee-description-wraps {
            position: absolute;
            bottom: 20px;
            display: flex;
            padding-left: 2%;
