                padding-bottom: 150px;
            }
            .fee-wrap-btn-box {
                @include setFeeWrapBtnBox();
                .month-subscribe-btn {
                    @include setSubscriptionWrapperBtn($yellow, black);
                }
                .year-subscribe-btn {
