                @include setSubscriptionWrapperHeadCopy(#c5c5c5, 15px, 15px);
            }
            .fee-wrap-body {
                @include setFeeWrapBody();
                padding-bottom: 100px;
            }
            .fee-wrap-btn-box {
                @include setFeeWrapBtnBox();

                .month-subscribe-btn {
                    @include setSubscriptionWrapperBtn(#A87DDF,white );
                }
                .year-subscribe-btn {
                    @include setSubscriptionWrapperBtn(#6A31A4, white);
                }
            }
        }
    }
    
}
